import React from 'react'
// Using Same Styles 
import './styles/FlatDetailSmall.css'

const BuildingDetailSmall = (props) => {

    const {name, type} = props.data
    return (
        <div className="flat--article" onClick={props.onBuildingClick}>
            {/* Uncomment to add a picture to the component */}
            {/* <img src={outline} 
                id="flat--img" 
                alt={clickIndex}>
            </img> */}
            <h4>{name}</h4>
            <h5>{type}</h5>
        </div>     
    )
}

export default BuildingDetailSmall


