// Flats in a Building
// 
import React, {useState} from 'react'

// Styles
import './styles/Flats.css'

//Component
import FlatDetailSmall from './FlatDetailSmall'
import BuildingDetailSmall from './BuildingDetailSmall'
import FlatDetails  from './FlatDetails'
import BuildingDetails from './BuildingDetails'
// import BuildingBriefMenu from './BuildingBriefMenu'


export const Flats = (props) => {

let { flats, building} = props
let buildingSmall = {
    name:building.name,
    outline:building.outline,
    clickIndex:building.clickIndex,
    tag:building.tag,
    type:building.type
}

// Keeping State 
    const [isSelected, setIsSelected] = useState(false)
    const [flatIndex, setFlatIndex] = useState(null)
// Default Opens on Building Details
    const [isBuildingSelect, setBuildingSelect] = useState(true)

// To Create Flat Brief Section
    let flatBrief = []
    const flatsSmall = flats.map((flat) => {
        let flatObj ={"id":flat.id, 
                        "name":flat.name, 
                        "outline":flat.outline, 
                        "fill":flat.fill, 
                        "area":flat.area, 
                        "type":flat.type, 
                    getFlatId(){
                        return flat.id
                    }
                    }
            flatBrief.push(flatObj)                
        return <FlatDetailSmall key={flatObj.id} data={flatObj} onFlatClick={showFlat}></FlatDetailSmall>
    })

// Flat Selection Event Handler
function showFlat(evt){
    if(!((evt.target.parentElement.className.match("flat--article flat--selected"))||(evt.target.parentElement.className.match("flat--article")))){
        return
    }
    if(!isSelected){
        let elementCleanUp = document.getElementsByClassName("flat--article flat--selected")
        if (elementCleanUp.length === 0) {
            evt.target.parentElement.className = "flat--article flat--selected"
            setBuildingSelect(false)
            setIsSelected(true)
            // console.log("L0")
        }else{
            for (let i = 0; i < elementCleanUp.length; i++) {
                elementCleanUp[i].className = "flat--article"
            }
            evt.target.parentElement.className = "flat--article flat--selected"
            setIsSelected(true)
            setBuildingSelect(false)
        }
    }else{
        let elementCleanUp = document.getElementsByClassName("flat--article flat--selected")
        if ( elementCleanUp.length === 0){
            evt.target.parentElement.className = "flat--article"
            setIsSelected(false)
            setBuildingSelect(false)
            // console.log("L3")

        }else{
            for (let i = 0; i < elementCleanUp.length; i++) {
                elementCleanUp[i].className = "flat--article";
            }
            evt.target.parentElement.className = "flat--article flat--selected"
            setIsSelected(true)
            setBuildingSelect(false)   
        }
    }
}

// Event Delegated to this Handler to set a Flat Index 
function checkIndex(evt){
    evt.stopPropagation()
    let elements = document.getElementsByClassName("flat--article flat--selected")
    if(elements.length === 0){
        return
    }else{
    try{
    let selectedFlatIndex = elements[0].children[3].innerText 
    setFlatIndex(selectedFlatIndex)
    }catch (error){
        console.log("No Selection")
    }
    }
}

function showBuilding(evt){
    evt.stopPropagation()
    let element = document.getElementsByClassName("flat--article flat--selected")
    if(element.length === 0){
        return
    }else {
        element[0].className = "flat--article"
        evt.target.parentElement.className = "flat--article flat--selected"
    }
    setBuildingSelect(true)
    setFlatIndex(null)    
}
return (
    <div className="flats" onClick={checkIndex}>
        {/* Make the Upper Bar */}
        <BuildingDetailSmall data={buildingSmall} onBuildingClick={showBuilding}></BuildingDetailSmall>
        {flatsSmall}
        {/* Rest of Details */}
        {isBuildingSelect?<BuildingDetails></BuildingDetails>:<React.Fragment></React.Fragment>}
        {flatIndex !== -1 && flatIndex !== null?<FlatDetails data={flats[flatIndex]}></FlatDetails>:<React.Fragment></React.Fragment>}
        {/* This lead to a unused leaf - Use it if required */}
        {/* <BuildingBriefMenu data={buildingSmall} onMenuClick={showBuilding}></BuildingBriefMenu>         */}
    </div>
    
    )
}
    
    
export default Flats


