import React from 'react'
import './styles/FlatDetailSmall.css'

const FlatDetailSmall = (props) => {

    const {name, area, type, id} = props.data
    return (
        <div className="flat--article" onClick={props.onFlatClick}>
            {/* Uncomment to Add a Picture to Component */}
            {/* <img src={outline} 
                id="flat--img" 
                alt={name}>
            </img> */}
            <h4>{name}</h4>
            <h5>{type}</h5>
            <h5>{area} ft<sup>2</sup></h5>
            <p>{id}</p>
        </div>     
    )
}

export default FlatDetailSmall


