import React from 'react'
import './styles/BuildingHeader.css'

const BuildingHeader = (props) => {

// Destructure Data
let { name, tag, gmaps, address } = props

// Event Handler    
function handleHover(evt, toValue) {
    evt.stopPropagation()
    let fromValue = evt.target.attributes.src.nodeValue
    if( fromValue === toValue)
    {
        evt.target.src = toValue
    }
    else{ 
        evt.target.src = toValue
    }
}
    
    return (
        <React.Fragment>
            <div className="building--header">
                <a id="contact--us" href="#contact_us_now">
                    <img src="/images/Contact.png" alt="Contact Us @ 9116354659/9911887878"/>
                </a>
                <div className="building">
                    <p id="name">{name}</p>
                    <p id="tag-line">{tag}</p>
                </div>
                <a id="map--link" target="_blank" href={gmaps}>
                    <img src="/images/LocationPin-Selected.png" 
                        alt="Go to Google Maps" 
                        onMouseOver={(e) => handleHover(e, "/images/LocationPin-Unselected.png")} 
                        onMouseLeave={(e) => handleHover(e, "/images/LocationPin-Selected.png")}
                    />
                </a>
            </div>
            <p id="building--address">{address}
            </p>  
        </React.Fragment>
    )
}

export default BuildingHeader



