// Think of it as a base component for now instead of an App itself
import React from 'react'
import './App.css'
import Building from './components/Building'
import Footer from './components/Footer'
import {buildingSpec} from './components/buildingdata/BuildingSpec'
import { flatSpec } from './components/buildingdata/FlatSpec'
import AdvertHeader from './components/AdvertHeader'

const App = () => {


  const headerObj = {
    name:buildingSpec.name,
    gmaps:buildingSpec.gmaps,
    address:buildingSpec.address,
    outline:buildingSpec.outline,
    tag:buildingSpec.tag,
    type:buildingSpec.type,
    clickIndex:buildingSpec.clickIndex
  }

return (
  <React.Fragment>
    <AdvertHeader></AdvertHeader>
    <Building header={headerObj} flats={flatSpec}></Building>
    <Footer></Footer>
  </React.Fragment>        
  )
}

export default App;



