import React from 'react'
import './styles/FlatDetails.css'

export default function FlatDetails(props) {
    
    let flat = props.data
    const createdTable = createRoomTable(flat)
    return (
        <div className="flat--details">
            <img src={flat.floorplan} id="flat--floorplan" alt="Floor Plan"></img>
            <div id="flat--info">
                    {createdTable}
            </div>
        </div>
    )
}



function createRoomTable(tableData) {
        if(!tableData){
            return
        }
        const {drawing, living, balcony, kitchen, bedrooms, dining, vestibule} = tableData
        const tableObj =  <table>
            <tbody>
            <tr>
                <th>Information\Space</th>
                <th>Drawing</th>
                <th>Living</th>
                <th>Balcony</th>
                <th>Kitchen</th>
                <th>Dining</th>
                <th>Vestibule</th>
                {bedrooms.map((bedroom, index) => <th key={index}>Bedroom {bedroom.id + 1}</th>)}
            </tr>
            <tr>
                <td>Area (Sq.Ft.)</td>
                <td>{drawing.length}x{drawing.breadth}</td>
                <td>{living.length}x{living.breadth}</td>
                <td>{balcony.length}x{balcony.breadth}</td>
                <td>{kitchen.length}x{kitchen.breadth}</td>
                <td>{dining.length}x{dining.breadth}</td>
                <td>{vestibule.length}x{vestibule.length}</td>
                {bedrooms.map((bedroom, index) => <td key={index}>{bedroom.length}x{bedroom.breadth}</td>)}
            </tr>
            <tr>
                <td>Balcony</td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                {bedrooms.map((room, index) => <td key={index}>{room.balcony.length}x{room.balcony.breadth}</td>)}
            </tr>
            <tr>
                <td>Wardrobe</td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                {bedrooms.map((room, index) => <td key={index}>{room.wardrobe.length}x{room.wardrobe.breadth}</td>)}
            </tr>
            <tr>   
                <td>Bathroom</td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                {bedrooms.map((room, index) => <td key={index}>{room.bathroom.length}x{room.bathroom.breadth}</td>)}
            </tr> 
            <tr>
                <td>Utility</td>
                <td></td>
                <td></td>
                <td></td>
                <td>{kitchen.utility.length}x{kitchen.utility.breadth}</td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
            </tr>  
            </tbody>

        </table>
    return tableObj
}




